import { filterByKey, normalizeOptions } from '../Helpers/Options';
import React, { useContext, useEffect, useRef, useState } from 'react'; // eslint-disable-line import/no-unresolved
import Api from '../Helpers/Api';
import { ReactComponent as CloseIcon } from '../../svg/x.svg';
import FormContext from '../FormContext';
import get from 'get-value';
import PropTypes from 'prop-types';

export default function Autocomplete({
	afterAdd,
	afterChange,
	clearable,
	clearButtonAttributes,
	clearButtonClassName,
	clearIconAttributes,
	clearIconHeight,
	clearIconWidth,
	clearText,
	disabled,
	id,
	inputClassName,
	labelFn,
	labelKey,
	max,
	name,
	optionButtonAttributes,
	optionButtonClassName,
	optionListAttributes,
	optionListClassName,
	optionListItemAttributes,
	optionListItemClassName,
	options,
	placeholder,
	readOnly,
	removeButtonAttributes,
	removeButtonClassName,
	removeIconAttributes,
	removeIconHeight,
	removeIconWidth,
	removeText,
	setValue,
	url,
	value,
	valueKey,
	wrapperAttributes,
	wrapperClassName,
	...otherProps
}) {
	const { formState } = useContext(FormContext);
	const clearButtonRef = useRef(null);
	const inputRef = useRef(null);
	const removeButtonRef = useRef(null);
	const [filter, setFilter] = useState('');
	const [isOpen, setIsOpen] = useState(false);
	const [highlightedIndex, setHighlightedIndex] = useState(0);
	const [optionValues, setOptionValues] = useState(options ? normalizeOptions(options, labelKey, valueKey) : []);

	useEffect(() => {
		if (url) {
			Api.get(url)
				.then((response) => {
					setOptionValues(normalizeOptions(response, labelKey, valueKey));
				});
		}
		return () => {};
	}, [url]);

	useEffect(() => {
		setOptionValues(options ? normalizeOptions(options, labelKey, valueKey) : []);
		return () => {};
	}, [options]);

	let currentValue = [];
	if (setValue !== null) {
		currentValue = value;
	} else {
		if (formState === undefined) {
			throw new Error('<Autocomplete> component must be inside a <Form> component.');
		}
		currentValue = get(formState.row, name);
	}
	if (currentValue === null || currentValue === undefined || currentValue === '') {
		currentValue = [];
	} else if (max === 1 && !Array.isArray(currentValue)) {
		currentValue = [currentValue];
	}

	const isSelected = (option) => (
		currentValue.findIndex((v) => (v.value === option.value)) > -1
	);

	let filteredOptions = [];
	if (filter) {
		filteredOptions = filterByKey(optionValues, 'label', filter);
		filteredOptions = filteredOptions.filter((option) => (!isSelected(option)));
	}

	const focus = () => {
		setTimeout(() => {
			if (inputRef.current) {
				inputRef.current.focus();
			}
		});
	};

	const addValue = (v) => {
		let newValue;
		if (max === 1) {
			newValue = v;
		} else {
			newValue = [...currentValue, v];
		}

		if (setValue) {
			setValue(newValue);
		} else {
			const e = { target: { name } };
			formState.setValues(formState, e, name, newValue, afterChange);
		}

		setIsOpen(false);
		setFilter('');
		if (max === 1) {
			setTimeout(() => {
				if (removeButtonRef.current) {
					removeButtonRef.current.focus();
				}
			});
		} else if (max === currentValue.length) {
			setTimeout(() => {
				if (clearButtonRef.current) {
					clearButtonRef.current.focus();
				}
			});
		} else {
			focus();
		}

		if (afterAdd) {
			afterAdd();
		}
	};

	const removeValue = (v) => {
		let newValue = [...currentValue];
		if (max !== 1) {
			const index = newValue.indexOf(v);
			if (index > -1) {
				newValue.splice(index, 1);
			}
		} else {
			newValue = '';
		}

		if (setValue) {
			setValue(newValue);
		} else {
			const e = { target: { name } };
			formState.setValues(formState, e, name, newValue, afterChange);
		}

		focus();
	};

	const onChange = (e) => {
		setFilter(e.target.value);
	};

	const onFocus = () => {
		setHighlightedIndex(0);
		setIsOpen(filter.length > 0);
	};

	const onKeyDown = (e) => {
		const filterValue = e.target.value;
		const numValues = currentValue ? currentValue.length : 0;
		if (e.key === 'Enter' && filterValue && filteredOptions.length > 0) {
			e.preventDefault();
		} else if (e.key === 'Backspace' && !filter && numValues > 0) {
			removeValue(currentValue[numValues - 1]);
		}
	};

	const onKeyUp = (e) => {
		const filterValue = e.target.value;
		if (e.key === 'Enter' && filterValue && filteredOptions.length > 0) {
			e.preventDefault();
			addValue(filteredOptions[highlightedIndex].value);
		} else if (e.key === 'ArrowDown') {
			if (highlightedIndex >= (filteredOptions.length - 1)) {
				setHighlightedIndex(0);
			} else {
				setHighlightedIndex(highlightedIndex + 1);
			}
		} else if (e.key === 'ArrowUp') {
			if (highlightedIndex > 0) {
				setHighlightedIndex(highlightedIndex - 1);
			} else {
				setHighlightedIndex(filteredOptions.length - 1);
			}
		} else if (e.key === 'Escape') {
			setIsOpen(false);
		} else {
			setHighlightedIndex(0);
			setIsOpen(filterValue.length > 0);
		}
	};

	const onClickOption = (e) => {
		let val = e.target.getAttribute('data-value');
		if (e.target.getAttribute('data-json') === 'true') {
			val = JSON.parse(val);
		}
		addValue(val);
	};

	const onClickRemoveOption = (e) => {
		let button = e.target;
		while (button && button.tagName.toUpperCase() !== 'BUTTON') {
			button = button.parentNode;
		}
		removeValue(currentValue[button.getAttribute('data-index')]);
	};

	const clear = () => {
		const newValue = [];

		if (setValue) {
			setValue(newValue);
		} else {
			const e = { target: { name } };
			formState.setValues(formState, e, name, newValue, afterChange);
		}

		setFilter('');
		focus();
	};

	const showClear = clearable && max !== 1 && currentValue.length > 0 && !disabled && !readOnly;

	let className = ['formosa-autocomplete'];
	if (showClear) {
		className.push('formosa-autocomplete--clearable');
	}
	className = className.join(' ');

	const canAddValues = !disabled && !readOnly && (max === null || currentValue.length < max);

	const wrapperProps = {};
	if (id || name) {
		wrapperProps.id = `${id || name}-wrapper`;
	}

	return (
		<div
			className={`${className} ${wrapperClassName}`.trim()}
			data-value={JSON.stringify(max === 1 && currentValue.length > 0 ? currentValue[0] : currentValue)} /* For testing. */
			{...wrapperProps}
			{...wrapperAttributes}
		>
			<div style={{ display: 'flex' }}>
				<ul className="formosa-autocomplete__values">
					{currentValue && currentValue.map((v, index) => {
						let val = v;
						let isJson = false;
						if (typeof val === 'object') {
							val = JSON.stringify(val);
							isJson = true;
						}

						const option = optionValues.find((o) => (
							isJson ? JSON.stringify(o.value) === val : o.value === val
						));

						let label = '';
						if (labelFn) {
							label = labelFn(option || v);
						} else if (option && Object.prototype.hasOwnProperty.call(option, 'label')) {
							label = option.label;
						}

						return (
							<li className="formosa-autocomplete__value formosa-autocomplete__value--item" key={val}>
								{label}
								{!disabled && !readOnly && (
									<button
										className={`formosa-autocomplete__value__remove ${removeButtonClassName}`.trim()}
										data-index={index}
										onClick={onClickRemoveOption}
										ref={removeButtonRef}
										type="button"
										{...removeButtonAttributes}
									>
										<CloseIcon height={removeIconHeight} width={removeIconWidth} {...removeIconAttributes} />
										{removeText}
									</button>
								)}
							</li>
						);
					})}
					{canAddValues && (
						<li className="formosa-autocomplete__value formosa-autocomplete__value--input">
							<input
								{...otherProps}
								autoComplete="off"
								className={`formosa-field__input formosa-autocomplete__input ${inputClassName}`.trim()}
								id={id || name}
								onChange={onChange}
								onFocus={onFocus}
								onKeyDown={onKeyDown}
								onKeyUp={onKeyUp}
								placeholder={placeholder}
								ref={inputRef}
								type="text"
								value={filter}
							/>
						</li>
					)}
				</ul>

				{isOpen && filteredOptions.length > 0 && (
					<ul className={`formosa-autocomplete__options ${optionListClassName}`.trim()} {...optionListAttributes}>
						{filteredOptions.map((option, index) => {
							let optionClassName = ['formosa-autocomplete__option'];
							if (highlightedIndex === index) {
								optionClassName.push('formosa-autocomplete__option--highlighted');
							}
							optionClassName = optionClassName.join(' ');

							let val = option.value;
							let isJson = false;
							if (typeof val === 'object') {
								isJson = true;
								val = JSON.stringify(val);
							}

							return (
								<li
									className={`${optionClassName} ${optionListItemClassName}`.trim()}
									key={val}
									{...optionListItemAttributes}
								>
									<button
										className={`formosa-autocomplete__option__button ${optionButtonClassName}`.trim()}
										data-json={isJson}
										data-value={val}
										onClick={onClickOption}
										type="button"
										{...optionButtonAttributes}
									>
										{option.label}
									</button>
								</li>
							);
						})}
					</ul>
				)}

				{showClear && (
					<div>
						<button
							className={`formosa-autocomplete__clear ${clearButtonClassName}`.trim()}
							onClick={clear}
							ref={clearButtonRef}
							type="button"
							{...clearButtonAttributes}
						>
							<CloseIcon height={clearIconHeight} width={clearIconWidth} {...clearIconAttributes} />
							{clearText}
						</button>
					</div>
				)}
			</div>
		</div>
	);
}

Autocomplete.propTypes = {
	afterAdd: PropTypes.func,
	afterChange: PropTypes.func,
	clearable: PropTypes.bool,
	clearButtonAttributes: PropTypes.object,
	clearButtonClassName: PropTypes.string,
	clearIconAttributes: PropTypes.object,
	clearIconHeight: PropTypes.number,
	clearIconWidth: PropTypes.number,
	clearText: PropTypes.string,
	disabled: PropTypes.bool,
	id: PropTypes.string,
	inputClassName: PropTypes.string,
	labelFn: PropTypes.func,
	labelKey: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.string,
	]),
	max: PropTypes.number,
	name: PropTypes.string,
	optionButtonAttributes: PropTypes.object,
	optionButtonClassName: PropTypes.string,
	optionListAttributes: PropTypes.object,
	optionListClassName: PropTypes.string,
	optionListItemAttributes: PropTypes.object,
	optionListItemClassName: PropTypes.string,
	options: PropTypes.oneOfType([
		PropTypes.array,
		PropTypes.object,
	]),
	placeholder: PropTypes.string,
	readOnly: PropTypes.bool,
	removeButtonAttributes: PropTypes.object,
	removeButtonClassName: PropTypes.string,
	removeIconAttributes: PropTypes.object,
	removeIconHeight: PropTypes.number,
	removeIconWidth: PropTypes.number,
	removeText: PropTypes.string,
	setValue: PropTypes.func,
	url: PropTypes.string,
	value: PropTypes.oneOfType([
		PropTypes.arrayOf(PropTypes.number),
		PropTypes.arrayOf(PropTypes.object),
		PropTypes.arrayOf(PropTypes.string),
		PropTypes.number,
		PropTypes.object,
		PropTypes.string,
	]),
	valueKey: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.string,
	]),
	wrapperAttributes: PropTypes.object,
	wrapperClassName: PropTypes.string,
};

Autocomplete.defaultProps = {
	afterAdd: null,
	afterChange: null,
	clearable: true,
	clearButtonAttributes: null,
	clearButtonClassName: '',
	clearIconAttributes: null,
	clearIconHeight: 12,
	clearIconWidth: 12,
	clearText: 'Clear',
	disabled: false,
	id: '',
	inputClassName: '',
	labelFn: null,
	labelKey: 'name',
	max: null,
	name: '',
	optionButtonAttributes: null,
	optionButtonClassName: '',
	optionListAttributes: null,
	optionListClassName: '',
	optionListItemAttributes: null,
	optionListItemClassName: '',
	options: null,
	placeholder: 'Search',
	readOnly: false,
	removeButtonAttributes: null,
	removeButtonClassName: '',
	removeIconAttributes: null,
	removeIconHeight: 12,
	removeIconWidth: 12,
	removeText: 'Remove',
	setValue: null,
	url: null,
	value: null,
	valueKey: null,
	wrapperAttributes: null,
	wrapperClassName: '',
};
