import { objectToString, stringToObject } from '../Helpers/Datetime';
import React, { useContext } from 'react'; // eslint-disable-line import/no-unresolved
import FormContext from '../FormContext';
import get from 'get-value';
import PropTypes from 'prop-types';
import Select from './Select';

export default function Datetime({
	afterChange,
	ampmAttributes,
	convertToTimezone,
	dayAttributes,
	disabled,
	hourAttributes,
	minuteAttributes,
	monthAttributes,
	name,
	secondAttributes,
	setValue,
	value,
	wrapperAttributes,
	wrapperClassName,
	yearAttributes,
}) {
	const { formState } = useContext(FormContext);

	let currentValue = '';
	if (setValue !== null) {
		currentValue = value;
	} else {
		if (formState === undefined) {
			throw new Error('<Datetime> component must be inside a <Form> component.');
		}
		currentValue = get(formState.row, name);
		if (currentValue === null || currentValue === undefined) {
			currentValue = '';
		}
	}
	console.log('currentValue', currentValue);

	const currentValueObject = stringToObject(currentValue, convertToTimezone);
	console.log('currentValueObject', currentValueObject);

	const onChange = (e) => {
		const key = e.target.getAttribute('data-datetime');
		const newValueObject = {
			...currentValueObject,
			[key]: e.target.value,
		};
		// setCurrentValueObject(newValueObject);
		console.log('newValueObject', JSON.stringify(newValueObject));

		const newValue = objectToString(newValueObject);
		console.log('newValue', newValue);

		// if (newValue.length === '0000-00-00 00:00:00'.length) {
			if (setValue) {
				setValue(newValue);
			} else {
				formState.setValues(formState, e, name, newValue, afterChange);
			}
		// }
	};

	return (
		<div className={`formosa-datetime-wrapper ${wrapperClassName}`.trim()} {...wrapperAttributes}>
			<Select
				data-datetime="month"
				disabled={disabled}
				id={`${name}-month`}
				options={{
					1: 'January',
					2: 'February',
					3: 'March',
					4: 'April',
					5: 'May',
					6: 'June',
					7: 'July',
					8: 'August',
					9: 'September',
					10: 'October',
					11: 'November',
					12: 'December',
				}}
				setValue={() => {
					onChange({ target: document.getElementById(`${name}-month`) });
				}}
				type="select"
				value={currentValueObject.month}
				wrapperClassName="formosa-field__input--date formosa-field__input--month"
				{...monthAttributes}
			/>
			<input
				className="formosa-field__input formosa-field__input--date formosa-field__input--day"
				data-datetime="day"
				disabled={disabled}
				id={`${name}-day`}
				inputMode="numeric"
				maxLength={2}
				onChange={onChange}
				placeholder="DD"
				size={4}
				type="text"
				value={currentValueObject.day}
				{...dayAttributes}
			/>
			<div className="formosa-suffix-container">
				<input
					className="formosa-field__input formosa-field__input--date formosa-field__input--year"
					data-datetime="year"
					disabled={disabled}
					id={`${name}-year`}
					inputMode="numeric"
					maxLength={4}
					onChange={onChange}
					placeholder="YYYY"
					size={6}
					type="text"
					value={currentValueObject.year}
					{...yearAttributes}
				/>
				<span className="formosa-suffix">,</span>
			</div>

			<div className="formosa-suffix-container">
				<input
					className="formosa-field__input formosa-field__input--date formosa-field__input--hour"
					data-datetime="hour"
					disabled={disabled}
					id={`${name}-hour`}
					inputMode="numeric"
					maxLength={2}
					onChange={onChange}
					placeholder="hh"
					size={4}
					type="text"
					value={currentValueObject.hour}
					{...hourAttributes}
				/>
				<span className="formosa-suffix">:</span>
			</div>
			<div className="formosa-suffix-container">
				<input
					className="formosa-field__input formosa-field__input--date formosa-field__input--minute"
					data-datetime="minute"
					disabled={disabled}
					id={`${name}-minute`}
					inputMode="numeric"
					maxLength={2}
					onChange={onChange}
					placeholder="mm"
					size={4}
					type="text"
					value={currentValueObject.minute}
					{...minuteAttributes}
				/>
				<span className="formosa-suffix">:</span>
			</div>
			<input
				className="formosa-field__input formosa-field__input--date formosa-field__input--second"
				data-datetime="second"
				disabled={disabled}
				id={`${name}-second`}
				inputMode="numeric"
				maxLength={2}
				onChange={onChange}
				placeholder="ss"
				size={4}
				type="text"
				value={currentValueObject.second}
				{...secondAttributes}
			/>
			<Select
				data-datetime="ampm"
				disabled={disabled}
				hideBlank
				id={`${name}-ampm`}
				options={{
					am: 'am',
					pm: 'pm',
				}}
				setValue={() => {
					onChange({ target: document.getElementById(`${name}-ampm`) });
				}}
				type="select"
				value={currentValueObject.ampm}
				wrapperClassName="formosa-field__input--date formosa-field__input--ampm"
				{...ampmAttributes}
			/>

			<input
				name={name}
				type="hidden"
				value={currentValue}
			/>
		</div>
	);
}

Datetime.propTypes = {
	afterChange: PropTypes.func,
	ampmAttributes: PropTypes.object,
	convertToTimezone: PropTypes.string,
	dayAttributes: PropTypes.object,
	disabled: PropTypes.bool,
	hourAttributes: PropTypes.object,
	minuteAttributes: PropTypes.object,
	monthAttributes: PropTypes.object,
	name: PropTypes.string,
	secondAttributes: PropTypes.object,
	setValue: PropTypes.func,
	value: PropTypes.string,
	wrapperAttributes: PropTypes.object,
	wrapperClassName: PropTypes.string,
	yearAttributes: PropTypes.object,
};

Datetime.defaultProps = {
	afterChange: null,
	ampmAttributes: null,
	convertToTimezone: 'UTC',
	dayAttributes: null,
	disabled: false,
	hourAttributes: null,
	minuteAttributes: null,
	monthAttributes: null,
	name: '',
	secondAttributes: null,
	setValue: null,
	value: null,
	wrapperAttributes: null,
	wrapperClassName: '',
	yearAttributes: null,
};
